body {
  /* make it centerd in a page */
  margin: auto; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  font-family: 'Red Hat Display', sans-serif;
  background-color: black;
  color: white;
  text-align: center;
  font-family: 'Red Hat Display', sans-serif;
  font-size: 0.8rem;
  max-width: 1200px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  color: white;
}

p{
  color: #C4C4C4;
  font-size: 0.9rem;
}

h3{
  color: #03BFCA;
}

/* btns */
.btn {
  border: 1px solid #03BFCA;
  border-radius: 5px;
  color: #03BFCA;
  padding: 8px 20px;
  margin: 3px;
  font-weight: 700;
}

.blue {
  background-color: #03BFCA;
  color: black;
}

.main_btn{
  border-radius: 5px;
  padding: 15px 25px;
  font-weight: 700;
  background-color: #03BFCA;
  color: black;
  margin:10px auto; 
  width: 125px;
  text-transform: uppercase;
}

/************ header *************/
header{
  padding-top: 20px;
  /* max-width: 1000px; */
}

.nav_logo {
  float: left;
  margin-left: 30px;
  font-weight: 900;
  font-style: italic;
  text-decoration: line-through;
  font-size: 1.4rem;
}

.nav_links{
  margin-left: 20px;
  display: inline-block;
  font-size: 0.9rem;
  font-weight: 500;
  font-style: normal;

}
.nav_link {
  padding: 10px;
}

.nav_btn {
  float: right;   
  margin: 10px 30px; 
}



.section_one {
  min-height: 700px;
  background-image: url('background.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */

  /* This is what makes the background image rescale based
  on the containerâ€™s size */
  background-size: cover;
  
  /* Set a background color that will be displayed
  while the background image is loading */
  background-color: #464646;

}


.Xlarge_heading {
  font-weight: 900;
  font-style: italic;
  font-size: 2.5rem;
  max-width: 900px;
  margin: 200px 150px;
}

.large_heading {
  font-weight: 900;
  font-size: 1.5rem;
}

/* section 2 */

.section_two {
  margin: 200px 150px;
  margin-bottom: 100px;
  background-color: lin;

}
.figures{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.left_figures{
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
}

.figure{
  margin-bottom: 40px;
}

.img_blue{
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #03BFCA;
  margin-right: 20px;
  vertical-align: top;
  margin-top: 20px;
}
/* footer */

.footer_links{
  padding: 10px;
}

.footer_link{
  padding: 0px 20px;
  /* display: inline; */
}

.footer_icons{
  display: flexbox;
  flex-direction: row;
  /* border: 4px solid rgb(82, 45, 45); */
  /* align-items: baseline; */
  /* justify-content: space-evenly; */
  padding:5px;
}

.footer_icon{
  margin:15px;
}


@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
}

.inline{
  display: inline-block;
}

.block{
  display: block;
}